import { useState, useEffect, useCallback } from 'react';
import {
  Spin,
  Typography,
} from 'antd';
import { useApp } from '../contexts/AppContext';
import { ACTION_TYPES } from '../contexts/AppContext';
import apiService from '../services/apiService';
import { setupErrorSuppression } from '../utils/safeFetch';
import { 
  StateHeader, 
  ResourcesByCategory 
} from '../components';

const { Text } = Typography;

const InfrastructureStatePage = () => {
  const { state, dispatch } = useApp();
  const [infrastructureData, setInfrastructureData] = useState(null);
  const [refreshing, setRefreshing] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');

  // Define functions before they're used in callbacks
  const loadInfrastructureState = useCallback(async (forceFresh = false) => {
    setRefreshing(true);
    dispatch({
      type: ACTION_TYPES.SET_LOADING,
      payload: { key: 'state', value: true },
    });

    try {
      const data = await apiService.getInfrastructureState(forceFresh);
      setInfrastructureData(data);
      
      dispatch({
        type: ACTION_TYPES.SET_INFRASTRUCTURE_STATE,
        payload: data,
      });
    } catch (error) {
      console.error('Failed to load infrastructure state:', error);
    } finally {
      setRefreshing(false);
      dispatch({
        type: ACTION_TYPES.SET_LOADING,
        payload: { key: 'state', value: false },
      });
    }
  }, [dispatch]);

  // Infrastructure State specific WebSocket message handler
  const handleInfrastructureStateWebSocketMessage = useCallback((data) => {
    console.log('InfrastructureStatePage: Handling WebSocket message:', data.type, data);
    
    switch (data.type) {
      case 'state_update':
        setInfrastructureData(data.data);
        dispatch({
          type: ACTION_TYPES.SET_INFRASTRUCTURE_STATE,
          payload: data.data,
        });
        break;
      
      case 'processing_completed':
        if (data.success) {
          setTimeout(() => {
            loadInfrastructureState(false);
          }, 1000);
        }
        break;
      
      default:
        break;
    }
  }, [dispatch, loadInfrastructureState]);

  // Register this page as active and setup WebSocket message handler
  useEffect(() => {
    // Setup global error suppression for fetch errors
    const cleanup = setupErrorSuppression();
    
    dispatch({
      type: ACTION_TYPES.SET_CURRENT_PAGE,
      payload: 'infrastructure-state',
    });

    // Register page-specific WebSocket message handler
    dispatch({
      type: ACTION_TYPES.REGISTER_PAGE_HANDLER,
      payload: {
        page: 'infrastructure-state',
        handler: handleInfrastructureStateWebSocketMessage,
      },
    });

    // Cleanup when component unmounts
    return () => {
      cleanup(); // Remove error suppression listeners
      
      dispatch({
        type: ACTION_TYPES.UNREGISTER_PAGE_HANDLER,
        payload: 'infrastructure-state',
      });
      
      dispatch({
        type: ACTION_TYPES.SET_CURRENT_PAGE,
        payload: null,
      });
    };
  }, [dispatch, handleInfrastructureStateWebSocketMessage]);

  // Load state on component mount
  useEffect(() => {
    loadInfrastructureState();
  }, [loadInfrastructureState]);

  // Process and filter resources with category grouping
  const getFilteredResourcesByCategory = () => {
    if (!infrastructureData) return {};

    let resources = {};
    
    const hasDiscoveredResources = infrastructureData.resources && infrastructureData.resources.length > 0;
    const managedResourceCount = infrastructureData.managed_state ? 
      Object.keys(infrastructureData.managed_state.resources || {}).length : 0;

    if (infrastructureData.managed_state && managedResourceCount > 0) {
      resources = infrastructureData.managed_state.resources || {};
    } else if (hasDiscoveredResources) {
      resources = {};
      infrastructureData.resources.forEach((resource, index) => {
        resources[resource.id || `resource-${index}`] = {
          id: resource.id,
          name: resource.name || resource.id,
          type: resource.type,
          status: resource.status,
          properties: {
            aws_details: resource.properties
          },
          createdAt: resource.createdAt,
          dependencies: resource.properties?.dependencies
        };
      });
    }

    // Filter out step_reference resources and apply search
    const filteredResources = Object.values(resources)
      .filter(r => r.type !== 'step_reference')
      .filter(r => !searchTerm || r.id.toLowerCase().includes(searchTerm.toLowerCase()));

    // Group resources by type (category)
    const resourcesByCategory = filteredResources.reduce((acc, resource) => {
      const type = resource.type || 'unknown';
      if (!acc[type]) {
        acc[type] = [];
      }
      acc[type].push(resource);
      return acc;
    }, {});

    return resourcesByCategory;
  };

  const resourcesByCategory = getFilteredResourcesByCategory();
  const totalResources = Object.values(resourcesByCategory).flat().length;

  return (
    <div style={{ 
      padding: '24px', 
      minHeight: '100vh',
      backgroundColor: '#fafafa'
    }}>
      <StateHeader
        onRefresh={() => loadInfrastructureState(false)}
        refreshing={refreshing}
        searchTerm={searchTerm}
        onSearchChange={(e) => setSearchTerm(e.target.value)}
        totalResources={totalResources}
        totalCategories={Object.keys(resourcesByCategory).length}
      />

      {/* Content */}
      {state.loading.state ? (
        <div style={{ 
          textAlign: 'center', 
          padding: '80px 0',
          backgroundColor: 'white',
          borderRadius: '16px',
          border: '1px solid #e8e8e8'
        }}>
          <Spin size="large" />
          <div style={{ marginTop: 24 }}>
            <Text type="secondary" style={{ fontSize: '16px' }}>
              Loading infrastructure resources...
            </Text>
          </div>
        </div>
      ) : (
        <ResourcesByCategory 
          infrastructureData={infrastructureData} 
          searchTerm={searchTerm}
        />
      )}
    </div>
  );
};

export default InfrastructureStatePage;